:root {
	--cl-grey: #9E9E9E;
	--cl-grey-dark: #616161;
	--cl-grey-light: #f4f4f4;
	--cl-selected: #e8e7e7;

	--cl-white-opace: #ffffffeb;
	--cl-grey-opace: #d2d2d2a1;

	--cl-yellow: #FFECB3;
	--cl-red: #ffa9a2;
	--cl-green: #b3d888;
	--cl-blue: #99c6f7;
	--cl-orange: #ffb74d;

	--shadow-xl: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);
	--shadow-l: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
	--shadow-m: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
	--shadow-m-inset: inset 0 10px 20px rgba(0, 0, 0, 0.19), inset 0 6px 6px rgba(0, 0, 0, 0.23);
	--shadow-m-w: 0 10px 20px rgba(255, 255, 255, 0.19), 0 6px 6px rgba(255, 255, 255, 0.23);
	--shadow-s: 0 3px 6px rgba(0,0,0,0.16), 0 0px 4px rgba(0,0,0,0.23);
	--shadow-s-inset: inset 0 3px 6px rgba(0,0,0,0.16), inset 0 0px 4px rgba(0,0,0,0.23);
	--shadow-s-m: 0 3px 6px rgba(255, 255, 255, 0.19), 0 0px 4px rgba(255, 255, 255, 0.19);
	--shadow-xs: 0 1px 3px rgba(0,0,0,0.12), 0 1px 2px rgba(0,0,0,0.24);
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
  color: var(--cl-grey-dark);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

main {
  display: grid;
  min-height: 100vh;
  width: 100%;
}

button {
	background-color: inherit;
	border: none;
	outline: none;
	line-height: 1;
	cursor: pointer;
}
button * {
	cursor: pointer;
}

button:disabled {
	background-color: white !important;
}

button:disabled > i {
	color: var(--cl-grey);
	pointer-events: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1, h2, h3, h4 {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
	color: inherit;
	display: grid;
	align-items: center;
}

ul, p {
	margin: 0;
	padding: 0;
	list-style-type: none;
}

input {
  padding: 0.8em 0.5em;
  border: 1px solid var(--cl-grey-light);
  border-radius: 8px;
}

textarea {
	border: none;
}

textarea:focus, input:focus {
	outline: none;
}

.form-field {
  display: grid;
  justify-items: start;
}

form {
	display: grid;
	min-width: 350px;
}

form > label {
	display: grid;
	justify-items: start;
}

form > label > input {
	justify-self: stretch;
}

i {
	color: var(--cl-grey-dark);
	cursor: pointer;
}

.icon-disabled {
	color: var(--cl-grey);
	cursor: not-allowed;
	pointer-events: none;
}

.selected {
	background-color: var(--cl-grey-light);
}

.bg-white {
	background-color: white;
}
.bg-black {
	background-color: black;
}
.bg-grey {
	background-color: var(--cl-grey);
}
.bg-grey-light {
	background-color: var(--cl-grey-light);
}
.bg-grey-dark {
	background-color: var(--cl-grey-dark);
}

.rounded {
	border-radius: 8px;
}

.grid {
  display: grid;
}
.centered-grid {
	display: grid;
  place-items: center;
}
.aligned-grid {
	display: grid;
	align-items: center;
}
.stretched-grid {
	display: grid;
	place-items: stretch;
}

.grid-tc-1m {
	grid-template-columns: 1fr max-content;
}
.grid-tc-m1 {
	grid-template-columns: max-content 1fr;
}
.grid-tc-m1m {
  grid-template-columns: max-content 1fr max-content;
}
.grid-tc-mm1 {
	grid-template-columns: max-content max-content 1fr;
}
.grid-tc-1mm {
	grid-template-columns: 1fr max-content max-content;
}
.grid-tc-m11m {
  grid-template-columns: max-content 1fr 1fr max-content;
}
.grid-tc-m1mm {
	grid-template-columns: max-content 1fr max-content max-content;
}
.grid-tc-mm1m {
	grid-template-columns: max-content max-content 1fr max-content;
}
.grid-tr-1m {
	grid-template-rows: 1fr max-content;
}
.grid-tr-m1 {
  grid-template-rows: max-content 1fr;
}
.grid-tr-mm1 {
  grid-template-rows: max-content max-content 1fr;
}
.grid-tr-m1mm {
	grid-template-rows: max-content 1fr max-content max-content;
}

.gap-xs {
	gap: 2px;
}
.gap-s {
  gap: 0.5em;
}
.gap-m {
	gap: 1em;
}
.cgap-m {
	column-gap: 1em;
}
.gap-l {
	gap: 2em;
}

.overflow-h {
  overflow: hidden;
}
.overflow-a {
	overflow: auto;
}

.full-screen {
  height: 100%;
  width: 100vw;
}

.shadow-s {
	box-shadow: var(--shadow-s);
}
.shadow-m {
	box-shadow: var(--shadow-m);
}
.shadow-l {
	box-shadow: var(--shadow-l);
}
.shadow-xl {
	box-shadow: var(--shadow-xl);
}

.pd-05 {
	padding: 0.5em
}
.pd-1 {
	padding: 1em;
}
.pd-2 {
	padding: 2em;
}
.pd-051 {
	padding: 0.5em 1em;
}
.pd-0515 {
	padding: 0.5em 1.5em;
}
.pd-01 {
	padding: 0 1em;
}
.pd-005 {
	padding: 0 0.5em;
}
.pd-01501 {
	padding: 0 1.5em 0 1em;
}
.pd-051051 {
	padding: 0.5em 1em;
}
.pd-1010 {
	padding: 1em 0;
}
.pd-1311 {
	padding: 1em;
	padding-right: 3em;
}

.z1 {
	z-index: 1;
}
.z2 {
	z-index: 2;
}