:root {
	--cl-grey: #9E9E9E;
	--cl-grey-dark: #616161;
	--cl-grey-light: #f4f4f4;
	--cl-selected: #e8e7e7;

	--cl-white-opace: #ffffffeb;
	--cl-grey-opace: #d2d2d2a1;

	--cl-yellow: #FFECB3;
	--cl-red: #ffa9a2;
	--cl-green: #b3d888;
	--cl-blue: #99c6f7;
	--cl-orange: #ffb74d;

	--shadow-xl: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);
	--shadow-l: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
	--shadow-m: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
	--shadow-m-inset: inset 0 10px 20px rgba(0, 0, 0, 0.19), inset 0 6px 6px rgba(0, 0, 0, 0.23);
	--shadow-m-w: 0 10px 20px rgba(255, 255, 255, 0.19), 0 6px 6px rgba(255, 255, 255, 0.23);
	--shadow-s: 0 3px 6px rgba(0,0,0,0.16), 0 0px 4px rgba(0,0,0,0.23);
	--shadow-s-inset: inset 0 3px 6px rgba(0,0,0,0.16), inset 0 0px 4px rgba(0,0,0,0.23);
	--shadow-s-m: 0 3px 6px rgba(255, 255, 255, 0.19), 0 0px 4px rgba(255, 255, 255, 0.19);
	--shadow-xs: 0 1px 3px rgba(0,0,0,0.12), 0 1px 2px rgba(0,0,0,0.24);
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
  color: #616161;
  color: var(--cl-grey-dark);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

main {
  display: grid;
  min-height: 100vh;
  width: 100%;
}

button {
	background-color: inherit;
	border: none;
	outline: none;
	line-height: 1;
	cursor: pointer;
}
button * {
	cursor: pointer;
}

button:disabled {
	background-color: white !important;
}

button:disabled > i {
	color: #9E9E9E;
	color: var(--cl-grey);
	pointer-events: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1, h2, h3, h4 {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
	color: inherit;
	display: grid;
	align-items: center;
}

ul, p {
	margin: 0;
	padding: 0;
	list-style-type: none;
}

input {
  padding: 0.8em 0.5em;
  border: 1px solid #f4f4f4;
  border: 1px solid var(--cl-grey-light);
  border-radius: 8px;
}

textarea {
	border: none;
}

textarea:focus, input:focus {
	outline: none;
}

.form-field {
  display: grid;
  justify-items: start;
}

form {
	display: grid;
	min-width: 350px;
}

form > label {
	display: grid;
	justify-items: start;
}

form > label > input {
	justify-self: stretch;
}

i {
	color: #616161;
	color: var(--cl-grey-dark);
	cursor: pointer;
}

.icon-disabled {
	color: #9E9E9E;
	color: var(--cl-grey);
	cursor: not-allowed;
	pointer-events: none;
}

.selected {
	background-color: #f4f4f4;
	background-color: var(--cl-grey-light);
}

.bg-white {
	background-color: white;
}
.bg-black {
	background-color: black;
}
.bg-grey {
	background-color: #9E9E9E;
	background-color: var(--cl-grey);
}
.bg-grey-light {
	background-color: #f4f4f4;
	background-color: var(--cl-grey-light);
}
.bg-grey-dark {
	background-color: #616161;
	background-color: var(--cl-grey-dark);
}

.rounded {
	border-radius: 8px;
}

.grid {
  display: grid;
}
.centered-grid {
	display: grid;
  place-items: center;
}
.aligned-grid {
	display: grid;
	align-items: center;
}
.stretched-grid {
	display: grid;
	place-items: stretch;
}

.grid-tc-1m {
	grid-template-columns: 1fr -webkit-max-content;
	grid-template-columns: 1fr max-content;
}
.grid-tc-m1 {
	grid-template-columns: -webkit-max-content 1fr;
	grid-template-columns: max-content 1fr;
}
.grid-tc-m1m {
  grid-template-columns: -webkit-max-content 1fr -webkit-max-content;
  grid-template-columns: max-content 1fr max-content;
}
.grid-tc-mm1 {
	grid-template-columns: -webkit-max-content -webkit-max-content 1fr;
	grid-template-columns: max-content max-content 1fr;
}
.grid-tc-1mm {
	grid-template-columns: 1fr -webkit-max-content -webkit-max-content;
	grid-template-columns: 1fr max-content max-content;
}
.grid-tc-m11m {
  grid-template-columns: -webkit-max-content 1fr 1fr -webkit-max-content;
  grid-template-columns: max-content 1fr 1fr max-content;
}
.grid-tc-m1mm {
	grid-template-columns: -webkit-max-content 1fr -webkit-max-content -webkit-max-content;
	grid-template-columns: max-content 1fr max-content max-content;
}
.grid-tc-mm1m {
	grid-template-columns: -webkit-max-content -webkit-max-content 1fr -webkit-max-content;
	grid-template-columns: max-content max-content 1fr max-content;
}
.grid-tr-1m {
	grid-template-rows: 1fr -webkit-max-content;
	grid-template-rows: 1fr max-content;
}
.grid-tr-m1 {
  grid-template-rows: -webkit-max-content 1fr;
  grid-template-rows: max-content 1fr;
}
.grid-tr-mm1 {
  grid-template-rows: -webkit-max-content -webkit-max-content 1fr;
  grid-template-rows: max-content max-content 1fr;
}
.grid-tr-m1mm {
	grid-template-rows: -webkit-max-content 1fr -webkit-max-content -webkit-max-content;
	grid-template-rows: max-content 1fr max-content max-content;
}

.gap-xs {
	grid-gap: 2px;
	gap: 2px;
}
.gap-s {
  grid-gap: 0.5em;
  gap: 0.5em;
}
.gap-m {
	grid-gap: 1em;
	gap: 1em;
}
.cgap-m {
	grid-column-gap: 1em;
	-webkit-column-gap: 1em;
	        column-gap: 1em;
}
.gap-l {
	grid-gap: 2em;
	gap: 2em;
}

.overflow-h {
  overflow: hidden;
}
.overflow-a {
	overflow: auto;
}

.full-screen {
  height: 100%;
  width: 100vw;
}

.shadow-s {
	box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 0px 4px rgba(0,0,0,0.23);
	box-shadow: var(--shadow-s);
}
.shadow-m {
	box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
